#graphic_ctn_outer #graphic_ctn {
  min-height: 100vh;
  width: 100vw;
}
#graphic_ctn_outer #graphic_ctn div,
#graphic_ctn_outer #graphic_ctn canvas {
  min-height: 100vh;
}
#graphic_ctn_outer .timeline {
  position: absolute;
  left: 50%;
  bottom: 5rem;
  width: 32rem;
  transform: translateX(-50%);
  z-index: 100;
}
@media only screen and (min-width: 50em) {
  #graphic_ctn_outer .timeline {
    bottom: 2rem;
  }
}
#graphic_ctn_outer .timeline .label {
  font-size: 12px;
}
#graphic_ctn_outer .timeline #dateLabelCtn {
  position: absolute;
  left: 0px;
  top: -0.5rem;
  max-width: 100px;
  font-size: 12px;
}
#graphic_ctn_outer .timeline #dateStart {
  position: absolute;
  left: -50px;
  top: 10px;
}
#graphic_ctn_outer .timeline #dateEnd {
  position: absolute;
  right: -50px;
  top: 10px;
}
#graphic_ctn_outer .timeline input[type=range] {
  -webkit-appearance: none;
  margin: 18px 0;
  width: 100%;
  cursor: pointer;
}
#graphic_ctn_outer .timeline input[type=range]:focus {
  outline: none;
}
#graphic_ctn_outer .timeline input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 3px;
  box-shadow: 1px 1px 1px #000, 0px 0px 1px #0d0d0d;
  background: #fff;
  border-radius: 1px;
  border: 0.2px solid #010101;
}
#graphic_ctn_outer .timeline input[type=range]::-webkit-slider-thumb {
  box-shadow: 1px 1px 1px #000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000;
  height: 16px;
  width: 4px;
  border-radius: 1px;
  background: #fff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -8px;
}
#graphic_ctn_outer .timeline input[type=range]:focus::-webkit-slider-runnable-track {
  background: #fff;
}
.inline-media-container {
  width: 90%;
  max-width: 40em;
  max-height: 40em;
  margin: auto;
}
.inline-media-container img,
.inline-media-container video {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
.caption {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #fff;
  text-align: left;
  font-style: italic;
  font-size: 0.8em;
  line-height: 1.2em;
}
#intro {
  position: relative;
  height: auto;
}
#intro video {
  position: absolute;
  top: 0;
  height: calc(100vh - 55px);
  width: 100%;
  object-fit: cover;
}
#intro-video-container {
  top: 55px;
  position: -webkit-sticky;
  position: sticky;
  height: calc(100vh - 55px);
  background-color: #000;
}
#intro-base-video {
  opacity: 0;
}
#title,
#subtitle {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 1em;
  position: relative;
  top: calc(55px - 50vh);
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: #fff;
  text-align: center;
  font-size: 2em;
}
@media only screen and (min-width: 40em) {
  #title,
  #subtitle {
    margin: 0 auto;
    font-size: 3em;
  }
}
#byline {
  position: relative;
  top: calc(55px - 50vh);
  text-align: center;
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 0.8em;
  margin: 40px auto auto 0px;
}
@media only screen and (min-width: 40em) {
  #byline {
    margin: 0 auto;
    font-size: 1.2em;
  }
}
#intro-scrolly-container {
  position: relative;
  z-index: 10;
  width: 100%;
  margin-top: -50vh;
}
#intro-scrolly-container :first-of-type ::first-line {
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  color: #a81854;
  font-style: normal;
  font-weight: 700;
  font-size: 2em;
}
.intro-scrolly-text {
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10vh;
  width: 80%;
  max-width: 40em;
  padding: 10px;
  color: #fff;
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1em;
  transform: translate(0, -50%);
}
.intro-scrolly-text:nth-last-child(2) {
  margin-bottom: 80vh;
}
@media only screen and (min-width: 40em) {
  .intro-scrolly-text {
    font-size: 1.2em;
  }
}
main {
  width: 100%;
  height: 100%;
  padding-top: 55px;
  box-sizing: border-box;
  position: relative;
  z-index: 0;
}
main .head_ctn {
  text-align: center;
}
main .head_ctn .headline {
  font-size: 2.5rem;
  max-width: 30rem;
  margin: 2rem auto;
}
main .head_ctn .dek {
  max-width: 30rem;
  margin: auto;
  line-height: 1.4;
}
main .head_ctn .intro_buttonCtn {
  margin-top: 1.5rem;
}
main .head_ctn .intro_buttonCtn .intro_button {
  padding: 4px 8px;
  background-color: #000;
  color: #fff;
  border: 1px solid #fff;
  align-items: center;
  height: 35px;
  margin: auto;
  display: flex;
  cursor: pointer;
  z-index: 10;
}
main .head_ctn .intro_buttonCtn .intro_button span {
  margin-right: 8px;
}
main .head_ctn .intro_buttonCtn .intro_button:hover {
  background-color: rgba(255,255,255,0.1);
}
.text-section p {
  width: 80%;
  margin: 1.5em auto;
  max-width: 40em;
  font-size: 1.2em;
  line-height: 1.3em;
}
.credits-section {
  width: 100%;
  margin: 3em auto;
  max-width: 40em;
}
.credits {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.2em;
  line-height: 1.2em;
}
.credits a {
  color: #fff;
  text-decoration: underline;
}
#credit-title {
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  font-style: normal;
  text-align: center;
  font-size: 2.4em;
}
@media only screen and (min-width: 50em) {
  #title-container {
    padding-top: 20vh;
  }
  .text-section p {
    width: 92%;
    margin: 1.5em auto;
  }
  .credits-section {
    width: 92%;
  }
}
#navbar {
  overflow: hidden;
  background-color: #000;
  height: 55px;
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  z-index: 10;
}
#navbar img {
  height: 100%;
  box-sizing: border-box;
}
.navbar-logo {
  display: flex;
  flex-flow: row;
  margin-left: 0.2em;
}
.navbar-social {
  height: 100%;
  display: flex;
  flex-flow: row;
  padding-right: 5px;
}
.navbar-social .nav-soc {
  max-width: 40px;
}
.navbar-social .nav-soc img {
  width: 100%;
}
.button-container {
  margin-left: 1em;
  margin-top: 1em;
}
@media only screen and (min-width: 40em) {
  .social-button {
    display: inline;
  }
}
.sub-button {
  color: #fff;
  padding: 0.4rem 0.8rem;
  border: #a81854 1px solid;
  border-radius: 4px;
  font-family: helvetica;
  font-weight: 400;
  font-style: normal;
  font-size: 0.8rem;
  text-transform: capitalize;
}
.sub-button:hover {
  background-color: #a81854;
}
.search form {
  max-width: 150px;
  margin-top: 10px;
}
.search form div {
  height: 30px;
  color: #000;
}
.search form div fieldset {
  border-color: #000 !important;
}
.njdc-links {
  margin: auto 10px;
  font-size: 0.8em;
}
@media only screen and (min-width: 40em) {
  .njdc-links {
    font-size: 1em;
    margin: auto 15px;
  }
}
@media only screen and (min-width: 60em) {
  .njdc-links {
    font-size: 1.2em;
  }
}
.njdc-links a {
  color: #000;
  font-family: "Merriweather", serif;
  font-weight: 400;
  font-size: 0.7em;
}
.njdc-links a:after {
  padding: 5px;
  content: "|";
}
@media only screen and (min-width: 40em) {
  .njdc-links a:after {
    padding: 10px;
  }
}
.njdc-links a:last-child::after {
  content: "";
}
.njdc-links a:hover {
  text-decoration: underline;
  -webkit-text-decoration-color: #a81854;
          text-decoration-color: #a81854;
  text-decoration-thickness: 20px;
  font-style: italic;
}
.sub-button-desktop {
  display: inline-block;
  background-color: #c6d653;
  border: solid 2px #c6d653;
  border: none;
  color: #000;
  margin-top: 10px;
  padding: 0.3em;
  border-radius: 5px;
  font-family: "Merriweather", serif;
  font-weight: 300;
  font-size: 0.5em;
  padding: 0.5em;
  font-size: 0.8em;
}
.scrolly-section {
  position: relative;
}
.scrolly-image-container {
  position: -webkit-sticky;
  position: sticky;
  top: 55px;
  width: 100%;
  height: calc(100vh - 55px);
  z-index: 0;
}
.scrolly-image {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transition: opacity 500ms 500ms;
}
.scrolly-image.visible {
  opacity: 1;
  transition: opacity 500ms;
}
.scrolly-text-container {
  position: relative;
  z-index: 10;
  padding-bottom: 1px;
}
.scrolly-text {
  width: 80%;
  max-width: 40em;
  margin: 0 auto;
  border: solid #000 1px;
  padding: 1em;
  background-color: #000;
  font-size: 1.2em;
}
.scrolly-text:not(:first-child) {
  margin: 100vh auto;
}
.sound_control_ctn {
  position: absolute;
  top: 5rem;
  left: 3rem;
  display: none;
}
.sound_control_ctn.active {
  display: inline;
}
.video-section {
  position: relative;
  height: calc(100vh - 55px);
}
.video-section video {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video-section h2 {
  position: absolute;
  top: calc((100vh - 55px) / 2);
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2.4em;
  z-index: 10;
  text-align: center;
  font-family: "ibm-plex-mono", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: #fff;
}
@media only screen and (min-width: 40em) {
  .video-section h2 {
    font-size: 3em;
    text-align: left;
  }
}
.video-overlay {
  z-index: 5;
}
